import { getEPALevel, getCloudCoverage } from './data-functions';

function showToday(responseData) {
  console.log(responseData);

  // Get data
  const { name, country, localtime  } = responseData.location;
  const [localDate, localTime] = localtime.split(' ');
  const { text, icon } = responseData.current.condition;
  const { last_updated, temp_c, temp_f, is_day, wind_mph, wind_kph, wind_degree, wind_dir, humidity, feelslike_c, feelslike_f, uv } = responseData.current;
  const { usEPAIndex } = getEPALevel(responseData);
  const { cloud } = getCloudCoverage(responseData);
  let temp;

  if (localStorage.getItem('temp') === 'celsius') {
    temp = `${temp_c}°C`;
  } else {
    temp = `${temp_f}°F`;
  }

  // Title
  const location = document.createElement('h2');
  const locationLastUpdated = document.createElement('span');
  locationLastUpdated.append(last_updated);
  location.append(`${name}, ${country}`);
  const top = document.createElement('div');
  top.className = 'top';
  top.append(location, locationLastUpdated);

  // Body
  const temperature = document.createElement('span');
  temperature.className = 'temperature';
  temperature.append(temp);
  const cloudCoverage = document.createElement('span');
  cloudCoverage.className = 'cloud-coverage';
  cloudCoverage.append(cloud);
  const currentForecast = document.createElement('div');
  currentForecast.append(temperature, cloudCoverage);

  const infoBox = document.querySelector('.info-box');
  infoBox.innerHTML = '';
  infoBox.append(top, currentForecast);
}

