export function showMainContent(responseData, currentBtn) {
  switch (true) {
    case currentBtn === 'today':
      showToday(responseData);
      break;
    case currentBtn === 'hourly':
      showHourly(responseData);
      break;
    case currentBtn === 'ten-day':
      showTenDay(responseData);
      break;
    case currentBtn === 'weekend':
      showWeekend(responseData);
      break;
    case currentBtn === 'monthly':
      showMonthly(responseData);
      break;
  }
}